import React, {Component} from "react";
import ProductListPriceFilterRowView from "./ProductListPriceFilterRowView"
import OnlineUtils from "eshop/utils/OnlineUtils";

class ProductListOneTimeFilterView extends React.Component {

    constructor(props){
        super(props);
        this.attributeId = this.props.header.replace(/\s+/g, '-').toLowerCase();
        this.componentId = props.id || OnlineUtils.generateUniqeHtmlId("priceFilterExpand_")
    }

    componentDidMount() {
    }

    componentDidUpdate() {
        if(document.getElementById(this.componentId)) {
            OPL.UI.initModules(document.getElementById(this.componentId));
        }
        if(document.getElementById("opl-product-list-one-time-expander-" + this.attributeId) && !document.getElementById("opl-product-list-one-time-expander-" + this.attributeId).getAttribute("data-js-processed")) {
            OPL.UI.initModules(document.getElementById("opl-product-list-one-time-expander-" + this.attributeId));
        }
    }

    render() {
        return (
            <div>
                {
                    this.props.showComponent ?
                        <div id={"opl-product-list-one-time-expander-" + this.attributeId}>
                            <div id={"opl-sticker-expander-main-" + this.attributeId}
                                 data-js-module="common/modules/opl-expander"
                                 data-js-conditions="element:{was seen}"
                                 data-js-options={JSON.stringify({
                                     scrollToSelected: false, triggerSelector: ".opl-section__trigger",
                                     "contentSelector": ".opl-section__content", "parentClass": "opl-section"
                                 })}
                                 role="tablist" aria-multiselectable="false">

                                <div className={"opl-section u-no-padding" + (this.props.initiallyExpanded? ' is-expanded' : '')}
                                     aria-expanded={(this.props.initiallyExpanded? 'true' : 'false')}>
                                    <div className="opl-section__header u-no-padding">
                                        <div className="o-separator u-spacing-l"></div>
                                        <div className="l-row">
                                            <div className="l-col l-col-9 ">
                                                <p className="h4 u-spacing-l">{this.props.header}</p>
                                            </div>
                                            <div className="l-col l-col-3 ">
                                                <a href="#"
                                                   className="u-text-right u-block opl-section__trigger opl-wiking-expander__trigger--plus"
                                                   role="tab">
                                                    <span
                                                        className="g-icon g-icon--only g-icon--plus g-icon--xs"></span>
                                                    <span className="u-acc-hide">Rozwiń sekcję</span>
                                                </a>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="opl-section__content u-hide--soft"
                                         role="tabpanel">
                                        <div className="l-row u-spacing-l u-no-margin"
                                             data-js-module="common/modules/opl-gradient-fade"
                                             data-js-options={JSON.stringify({
                                                 'coverClass': '.cover',
                                                 'triggerHideName': 'Zwiń',
                                                 'triggerShowName': 'Rozwiń',
                                                 'maxHeight': 300,
                                                 'showFirst': this.props.showExpanderNumber,
                                                 'scrollTop': false,
                                                 'scrollSet': 0
                                             })}>
                                            <ul className="opl-gradient-fade--wrapper">
                                                {this.props.tieredPrices && this.props.tieredPrices.map((price, index) =>
                                                    <ProductListPriceFilterRowView key={"Price_" + price + "_" + index}
                                                                                   type={this.props.type}
                                                                                   checked={this.props.selectedTieredPrices[price.id]}
                                                                                   onClick={() => this.props.onClick(price.id)}
                                                                                   text={this.props.labelFormatter(price)}
                                                    />
                                                )}
                                            </ul>
                                            <div
                                                className="opl-gradient-fade--trigger u-text-left u-spacing-m u-spacing-top">
                                                <a href="#" className="opl-load-more u-font-bold">Rozwiń</a></div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        : null
                }
            </div>
        )
    }
}

export default ProductListOneTimeFilterView;